@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Jua&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Jua&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App, body {
  text-align: center;
  height: 110vh;
  display : -webkit-flex;
  display : flex;
  -webkit-flex-direction : column;
          flex-direction : column;
  background: url(/jokes-gen/static/media/Background.9199dbe9.jpg) no-repeat center center fixed;
  background-size: cover;
}
.nav{
  height: 15vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-family: 'Press Start 2P', cursive;
}
.categories{
  height: 33vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  font-family: 'Jua', sans-serif;
  font-size: 25px;
}
.category{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex: 1 0 21%;
          flex: 1 0 21%;
  cursor: pointer;
  margin: 5px;
  color: #000000;
  text-transform: uppercase;
  border: 2px solid #e5dc15;
  border-radius: 0.5rem;
  box-shadow: 0 0.2em #c2bb11, 0 0.4em rgba(0, 0, 0, 0.4);
  position: relative;
  top: 0;
  transition: all 150ms ease-in-out;
}
.category:hover {
  background: #e5dc15;
  color: #0e79b2;
  top: 0.1em;
  box-shadow: 0 0.2em #c2bb11, 0 0.4em rgba(0, 0, 0, 0.4);
}
.category:active {
  top: 0.3em;
  box-shadow: 0 0.2em #c2bb11, 0 0.5em rgba(0, 0, 0, 0.4);
}
.CategorySelect{
  position: absolute;
  width: 99.8%;
  top: 49%;
  height: 8vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-family: 'Jua', sans-serif;
  font-size: 25px;
}
.jokes{
  height: 42vh;
  width: 99.8%;
  position: absolute;
  bottom: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: space-around;
          align-items: space-around;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 28px;
  font-weight: 500;
  }
  @media only screen and (max-width: 503px) {
    .App, body{
      height: 110vh;
    }
    .jokes{
      height: 32vh;
      font-size: 22px;
      position: absolute;
      bottom: 0;
    }
    .CategorySelect{
      top: 59%;
    }
    .categories{
      height: 43vh;
    }
  }

.joke{
    margin: 20px;
}
.btn{
    position: relative;
    top: 0;
    text-decoration: none;
    padding: 12px;
    border: 2px solid #e5dc15;
    border-radius: 0.5rem;
    box-shadow: 0 0.2em #c2bb11, 0 0.4em rgba(0, 0, 0, 0.4);
    transition: all 150ms ease-in-out;
    cursor: pointer;
    margin: 5px;
    color: #000000;
    font-size: 20px;
    font-family: 'Jua', sans-serif;
    background-color: transparent;
}
.btn:hover {
    background: #e5dc15;
    color: #0e79b2;
    top: 0.1em;
    box-shadow: 0 0.2em #c2bb11, 0 0.4em rgba(0, 0, 0, 0.4);
  }
  .btn:active {
    top: 0.3em;
    box-shadow: 0 0.2em #c2bb11, 0 0.5em rgba(0, 0, 0, 0.4);
  }
